.header{
    height: 60px;
    background: $primary-bg-color;
    @include box-shadow;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
    position: relative;
    z-index: 2;
    
    .left{
        display: inline-flex;
        
        .logo{
            padding: 16px 70px;
            font-size: 26px;
            font-family: 'Pangram ExtraBold';
            color: #fff;
            background: $secondary-bg-color;
            @include box-shadow;
            height: auto;
            cursor: pointer;
        }

        .menu{
            padding: 0px 20px;
            display: inline-flex;
            align-self: center;

            li{
                font-size: 16px;
                font-family: 'Pangram Regular';
                text-align: center;
                padding: 6px 14px;
                color: #fff;
                cursor: pointer;
            }
            
            li::before{
                content: attr(data-name);
                position: absolute;
                height: 2px;
                width: 0;
                background: $selected-color;
                margin-top: 22px;
                color: transparent;
                opacity: 0;
                pointer-events: none;
                transition: $transition;
            }
            
            li:hover::before{
                content: attr(data-name);
                position: absolute;
                height: 2px;
                width: auto;
                background: $selected-color;
                margin-top: 22px;
                color: transparent;
                pointer-events: none;
                opacity: 1;
                transition: $transition;
            }
        }
    }
    
    .right{
        display: inline-flex;
        
        .profile{
            background: $secondary-bg-color;
            @include box-shadow;
            height: auto;
            padding: 0px 40px;
            .user-infos{
                color: #fff;
                display: inline-block;
                padding: 12px 0;
                
                .avatar{
                    height: 38px;
                    width: 38px;
                    overflow: hidden;
                    border-radius: 100%;
                    background: $primary-bg-color;
                    text-align: center;
                    vertical-align: middle;
                    margin-right: 15px;
                }
                
                .username{
                    vertical-align: middle;
                    font-size: 15px;
                    font-family: 'Pangram Regular';
                }
                
                i{
                    vertical-align: middle;
                    margin-left: 5px;
                    margin-top: -5px;
                    cursor: pointer;
                }
            }
        }
    }
    
}